import React, { Fragment, lazy, Suspense } from "react";
import { Switch, Route } from "react-router-dom";
import "./App.css";

const Home = lazy(() => import("./pages/Home"));
const Perfil = lazy(() => import("./pages/Perfil"));
const Dashboard = lazy(() => import("./pages/Dashboard"));
const Clientes = lazy(() => import("./pages/Clientes"));
const Productos = lazy(() => import("./pages/Productos"));
const Rubros = lazy(() => import("./pages/Rubros"));

const App = () => {
  return (
    <Fragment>
      <Switch>
        <Route exact path="/">
          <Suspense fallback={<div className="uk-position-center uk-overlay uk-overlay-default"><span uk-spinner="ratio: 4.5"></span></div>}>
            <Home />
          </Suspense>
        </Route>
        <Route path="/perfil">
          <Suspense fallback={<div className="uk-position-center uk-overlay uk-overlay-default"><span uk-spinner="ratio: 4.5"></span></div>}>
            <Perfil />
          </Suspense>
        </Route>
        <Route exact path="/dashboard">
          <Suspense fallback={<div className="uk-position-center uk-overlay uk-overlay-default"><span uk-spinner="ratio: 4.5"></span></div>}>
            <Dashboard />
          </Suspense>
        </Route>
        <Route exact path="/dashboard-clientes">
          <Suspense fallback={<div className="uk-position-center uk-overlay uk-overlay-default"><span uk-spinner="ratio: 4.5"></span></div>}>
            <Clientes />
          </Suspense>
        </Route>
        <Route exact path="/dashboard-productos">
          <Suspense fallback={<div className="uk-position-center uk-overlay uk-overlay-default"><span uk-spinner="ratio: 4.5"></span></div>}>
            <Productos />
          </Suspense>
        </Route>
        <Route exact path="/dashboard-rubros">
          <Suspense fallback={<div className="uk-position-center uk-overlay uk-overlay-default"><span uk-spinner="ratio: 4.5"></span></div>}>
            <Rubros />
          </Suspense>
        </Route>
      </Switch>
    </Fragment>

  );
};

export default App;
