import axios from "axios";

export const FETCH_CLIENTES_REQUEST = "FETCH_CLIENTES_REQUEST";
export const FETCH_CLIENTES_SUCCESS = "FETCH_CLIENTES_SUCCESS";
export const FETCH_CLIENTES_ERROR = "FETCH_CLIENTES_ERROR";

const getClientesActions = (dispatch) => {
  dispatch({ type: FETCH_CLIENTES_REQUEST });

  axios
    .get("https://www.sundelimpieza.com/api/clientes/all") // https://www.sundelimpieza.com/api/
    .then(res => {
      dispatch({
        type: FETCH_CLIENTES_SUCCESS,
        payload: res.data
      });
    })
    .catch(error => {
      dispatch({
        type: FETCH_CLIENTES_ERROR,
        erorr: error.toString()
      });
    });
};

export default getClientesActions;