import { FETCH_PRODUCTOS_REQUEST, FETCH_PRODUCTOS_SUCCESS, FETCH_PRODUCTOS_ERROR } from '../actions/productosActions'

const initialState = {
  array: [],
  isFetching: false
};

function getProductosReducers(state = initialState, action) {
  switch (action.type) {
    case FETCH_PRODUCTOS_REQUEST:
      return {
        ...state,
        isFetching: true
      }
    case FETCH_PRODUCTOS_SUCCESS:
      return {
        ...state,
        isFetching: false,
        array: action.payload
      }
    case FETCH_PRODUCTOS_ERROR:
      return {
        ...state,
        isFetching: false,
      }
    default:
      return state;
  }
}

export default getProductosReducers;
