import { FETCH_RUBROS_REQUEST, FETCH_RUBROS_SUCCESS, FETCH_RUBROS_ERROR } from '../actions/rubrosActions'

const initialState = {
  array: [],
  isFetching: false
};

function getRubrosReducers(state = initialState, action) {
  switch (action.type) {
    case FETCH_RUBROS_REQUEST:
      return {
        ...state,
        isFetching: true
      }
    case FETCH_RUBROS_SUCCESS:
      return {
        ...state,
        isFetching: false,
        array: action.payload
      }
    case FETCH_RUBROS_ERROR:
      return {
        ...state,
        isFetching: false,
      }
    default:
      return state;
  }
}

export default getRubrosReducers;
