import { GET_USER_REQUEST, GET_USER_SUCCESS, GET_USER_ERROR } from '../actions/usersActions'

const initialState = {
    array: [],
    isFetching: false
  };

function getUserReducers(state = initialState, action) {
    switch (action.type) {
      case GET_USER_REQUEST:
        return {
          ...state,
          isFetching: true
        }
      case GET_USER_SUCCESS:
        return {
          ...state,
          isFetching: false,
          array: action.payload
        }
      case GET_USER_ERROR:
        return {
          ...state,
          isFetching: false,
        }
      default:
        return state;
    }
  }

export default getUserReducers;