import axios from "axios";

export const FETCH_RUBROS_REQUEST = "FETCH_RUBROS_REQUEST";
export const FETCH_RUBROS_SUCCESS = "FETCH_RUBROS_SUCCESS";
export const FETCH_RUBROS_ERROR = "FETCH_RUBROS_ERROR";

const getRubrosActions = (dispatch) => {
  dispatch({ type: FETCH_RUBROS_REQUEST });

  axios
    .get("https://www.sundelimpieza.com/api/rubros/all") // https://www.sundelimpieza.com/api/
    .then(res => {
      dispatch({
        type: FETCH_RUBROS_SUCCESS,
        payload: res.data
      });
    })
    .catch(error => {
      dispatch({
        type: FETCH_RUBROS_ERROR,
        erorr: error.toString()
      });
    });
};

export default getRubrosActions;