import { LOGIN_USER_REQUEST, LOGIN_USER_SUCCESS, LOGIN_USER_ERROR } from '../actions/signupActions'

const initialState = {
  array: [],
  isFetching: false
};

function registerUserReducers(state = initialState, action) {
  switch (action.type) {
    case LOGIN_USER_REQUEST:
      return {
        ...state,
        isFetching: true
      }
    case LOGIN_USER_SUCCESS:
      return {
        ...state,
        isFetching: false,
        array: action.payload
      }
    case LOGIN_USER_ERROR:
      return {
        ...state,
        isFetching: false,
      }
    default:
      return state;
  }
}

export default registerUserReducers;


