import axios from "axios";

export const FETCH_PRODUCTOS_REQUEST = "FETCH_PRODUCTOS_REQUEST";
export const FETCH_PRODUCTOS_SUCCESS = "FETCH_PRODUCTOS_SUCCESS";
export const FETCH_PRODUCTOS_ERROR = "FETCH_PRODUCTOS_ERROR";

const getProductosActions = (dispatch) => {
  dispatch({ type: FETCH_PRODUCTOS_REQUEST });

  axios
    .get("https://www.sundelimpieza.com/api/productos/all") // https://www.sundelimpieza.com/api/
    .then(res => {
      dispatch({
        type: FETCH_PRODUCTOS_SUCCESS,
        payload: res.data
      });
    })
    .catch(error => {
      dispatch({
        type: FETCH_PRODUCTOS_ERROR,
        erorr: error.toString()
      });
    });
};

export default getProductosActions;