import { FETCH_CLIENTES_REQUEST, FETCH_CLIENTES_SUCCESS, FETCH_CLIENTES_ERROR } from '../actions/clientesActions'

const initialState = {
  array: [],
  isFetching: false
};

function getClientesReducers(state = initialState, action) {
  switch (action.type) {
    case FETCH_CLIENTES_REQUEST:
      return {
        ...state,
        isFetching: true
      }
    case FETCH_CLIENTES_SUCCESS:
      return {
        ...state,
        isFetching: false,
        array: action.payload
      }
    case FETCH_CLIENTES_ERROR:
      return {
        ...state,
        isFetching: false,
      }
    default:
      return state;
  }
}

export default getClientesReducers;
