import { createStore, applyMiddleware, combineReducers, compose } from "redux";
import thunk from "redux-thunk";

import clientes from './reducers/clientesReducers'
import productos from './reducers/productosReducers'
import rubros from './reducers/rubrosReducers'
import signup from './reducers/signupReducers'
import users from './reducers/usersReducers'

let rootReducer = combineReducers({
    clientes,
    productos,
    rubros,
    signup,
    users,
})

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;

const store = createStore(
  rootReducer,
  composeEnhancers(applyMiddleware(thunk))
);

export default store;
